export default {
	data() {
		return {
			pswdText: '',
			pswd: false
		}
	},
	methods: {
		inputChange (e, val) {
			if (val == 'pswd') {
				this.pswdText = e.detail.value;
				if (e.detail.value) {
					this.pswd = true;
				} else {
					this.pswd = false;
				}
			}
		},
		clearPswd () {
			this.pswdText = '';
			this.pswd = false;
		},
		handleSubmit () {
			if (!this.pswdText) {
				uni.showToast({
					icon: 'none',
					title: '密码不能为空',
				});
				return false;
			} else {
				uni.setStorageSync('pswd', this.pswdText);
				uni.showToast({
					icon: 'none',
					title: '修改成功'
				});
				setTimeout( () => {
					uni.navigateTo({
						url:'../login/login'
					})
				}, 500)
			}
		}
	}
}
